@use 'base/variables' as v;
@use 'base/mixins' as m;

.slider {
    // position: fixed;
    // top: 0;
    // left: 0;
    // bottom: 0;
    // right: 0;
    // z-index: 1000000;
    // width: 100%;
    // height: 100%;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    // background-color: transparentize(v.$dark, .5);
    margin-bottom: 2rem;

    &__graphic {
        position: relative;
        max-height: 100vh;
        max-width: max-content;
    }

    &__image {
        max-height: 100%;

        @include m.device(v.$XLDesktop) {
            width: 200rem;
        }
    }

    &__btn {
        all: unset;
        transition: background-color .1s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        padding: 1rem;
        cursor: pointer;
        border-radius: 100%;
        &:hover {
            background-color: transparentize(black, .5);
            
        }

        &:focus {
            outline: none;
        }

        &:focus-visible {
            outline: .2rem solid v.$darkFontColor;
        }

        @include m.device(v.$tablet) {
            width: 12rem;
            height: 12rem;
        }
        
        > * {
            stroke: white;
            fill: white;
            width: 2rem;
            height: 2rem;
            
            

            
            @include m.device(v.$tablet) {
    
                width: 5rem;
                height: 5rem;
            }
        }
        
    }

    &__close-btn {
        all: unset;
        transition: background-color .1s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 2rem;
        right: 2rem;
        // padding: 1rem;
        cursor: pointer;
        border-radius: 100%;
        &:hover {
            background-color: transparentize(black, .5);
            
        }

        &:focus {
            outline: none;
        }

        &:focus-visible {
            outline: .2rem solid v.$darkFontColor;
        }

        @include m.device(v.$tablet) {
            width: 6rem;
            height: 6rem;
        }
        
        > * {
            stroke: white;
            fill: white;
            width: 2rem;
            height: 2rem;
            
            

            
            @include m.device(v.$tablet) {
    
                width: 3rem;
                height: 3rem;
            }
        }
        
    }

    &__test {
        height: 200vh;
    }
}